*{
  margin: 0;
  padding: 0;
}
.header{
  display: flex;
  justify-content: space-between;
  background-color: black;
  color:white;
  padding-left: 15px;
  padding-right: 25px;
  height: 80px;
  margin-bottom: 0px;
  
}
.heading{
  letter-spacing: 5.5px;
  padding-top: 15px;
}
.cart{
  border: 1px solid white;
  border-radius: 10px;
  height: 40px;
  margin-top: 10px;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
}
.cart-number{
  background-color: grey;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 50%;
}
.page{
  width: 100%;
  height: 200px;
  background-color: gray;
  text-align: center;
  margin-top: 0px;
}
.main-heading{
  letter-spacing: 4px;
  color: white;
  padding-top: 60px;
}
.itemSection{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap:20px;
  padding: 50px;
}
.itemCard{
  width: 350px;
  height:450px;
  border: 1px solid black;
  border-radius: 10px;
}
.image{
  width: 340px;
  height: 300px;
  object-fit: contain;
}
.name{
  margin-bottom: 5px;
}
.price{
  margin-bottom: 15px;
}
.specs{
  text-align: center;
}
.cartbutton{
  text-align: center;
  padding-bottom: 20px;
}
button{
  border: 1px solid black;
  border-radius: 20px;
  padding:10px;
  background-color: black;
  color:white;
  width: 150px;
  height: 50px;
}
.footer{
  width: 100%;
  height: 100px;
  background-color: black;
  color:white;
  text-align: center;
}
.footer > h3{
  padding-top: 40px;
}